import React from 'react';
 
export default class Spinner extends React.Component {
 
    render() {
        return(
            <div className="row">
                <div className="col-md-12 m-t-3">
                    <img 
                        className="img-responsive center-block"
                        src="https://cdn.filestackcontent.com/WANCBKNFS2i9DZremSFd" />
                </div>
            </div>
        );
    }
}


